.text-center {
    text-align: center;
}

.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.div-form {
    width: 500px;
    margin: 5em auto;
}

.div-form-fields {
    min-height: 200px;
    /* height: 700px; */
    width: 500px;
    position: relative;
}

.form-modal-edit {
    width: 650px;
}

.form-intro {
    width: 100%;
    /* position: absolute; */
    opacity: 0;
    animation-name: rightFadeInOut;
    animation-delay: 0s;
    animation-duration: 0.1s;
    animation-timing-function: ease-in;
    animation-fill-mode: forwards; /*use this animation property to retain the animation afterwards(100% animation keyframes)*/
    color: #ccc;
}

@keyframes rightFadeInOut {
    0% {
        /* position: absolute; */
        /* right: -10rem; */
        /* transform: translateX(150px) scale(0.2); */
        transform: scale(0.5);
        opacity: 0;
    }

    100% {
        /* right: 0rem; */
        /* transform: translateX(0px) scale(1); */
        transform: scale(1);
        opacity: 1;
    }
}


:root {
    --breadcrumb-theme-1: #e91e63;
    --breadcrumb-theme-2: #fff;
    --breadcrumb-theme-3: #d80d52;
    --breadcrumb-theme-4: #c70041;
}
.breadcrumb {
    text-align: center;
    display: inline-block;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);
    overflow: hidden;
    border-radius: 5px;
    counter-reset: flag;
}
.breadcrumb__step {
    text-decoration: none;
    outline: none;
    display: block;
    float: left;
    font-size: 12px;
    line-height: 36px;
    padding: 0 10px 0 60px;
    position: relative;
    background: var(--breadcrumb-theme-2);
    color: var(--breadcrumb-theme-1);
    transition: background 0.5s;
}
.breadcrumb__step:first-child {
    padding-left: 46px;
    border-radius: 5px 0 0 5px;
}
.breadcrumb__step:first-child::before {
    left: 14px;
}
.breadcrumb__step:last-child {
    border-radius: 0 5px 5px 0;
    padding-right: 20px;
}
.breadcrumb__step:last-child::after {
    content: none;
}
.breadcrumb__step::before {
    content: counter(flag);
    counter-increment: flag;
    border-radius: 100%;
    width: 20px;
    height: 20px;
    line-height: 20px;
    margin: 8px 0;
    position: absolute;
    top: 0;
    left: 30px;
    font-weight: bold;
    background: var(--breadcrumb-theme-2);
    box-shadow: 0 0 0 1px var(--breadcrumb-theme-1);
}
.breadcrumb__step::after {
    content: "";
    position: absolute;
    top: 0;
    right: -18px;
    width: 36px;
    height: 36px;
    transform: scale(0.707) rotate(45deg);
    z-index: 1;
    border-radius: 0 5px 0 50px;
    background: var(--breadcrumb-theme-2);
    transition: background 0.5s;
    box-shadow: 2px -2px 0 2px var(--breadcrumb-theme-4);
}
.breadcrumb__step--active {
    color: var(--breadcrumb-theme-2);
    background: var(--breadcrumb-theme-1);
}
.breadcrumb__step--active::before {
    color: var(--breadcrumb-theme-1);
}
.breadcrumb__step--active::after {
    background: var(--breadcrumb-theme-1);
}
.breadcrumb__step:hover {
    color: var(--breadcrumb-theme-2);
    background: var(--breadcrumb-theme-3);
}
.breadcrumb__step:hover::before {
    color: var(--breadcrumb-theme-1);
}
.breadcrumb__step:hover::after {
    color: var(--breadcrumb-theme-1);
    background: var(--breadcrumb-theme-3);
}
